import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Link } from "react-router-dom";
import { ColorConsumer } from "../../../../context/colorContext";
import ApiConfig from "../../../../api/config";

class CancelModal extends React.Component {
  state = {
    open: false,
    scroll: "paper"
  };
  static contextType = ColorConsumer;

  handleCancel = () => {
    this.setState({ open: false });
    this.props.handleModalNo();
  };

  componentWillReceiveProps(nextProps) {
    this.setState({
      open: nextProps.handleModalOpen
    });
  }

  changeColor = async () => {
    if ((await ApiConfig.getUser()) == "admin") {
      this.context.updateColor("#74b49b");
      this.context.updateAvatar(
        "data:image/png;base64,"
      );
    }
  };

  render() {
    return (
      <div>
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          scroll={this.state.scroll}
          aria-labelledby="scroll-dialog-title"
        >
          <DialogTitle id="scroll-dialog-title">
            ¿Seguro que quieres cancelar? Perderás los cambios realizados
          </DialogTitle>
          <DialogActions>
            <Link to="/filemng" style={{ textDecoration: "none" }}>
              <Button color="secondary" onClick={this.changeColor}>
                Sí
              </Button>
            </Link>
            <Button onClick={this.handleCancel} color="secondary">
              No
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default CancelModal;
