import React from "react";

import { TwitterPicker } from "react-color";

//Material UI
import Paper from "@material-ui/core/Paper";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import CardActionArea from "@material-ui/core/CardActionArea";
import resizer from "image-resizer-js";

//Self-made components
import Notification from "../../../../components/notification";
import Input from "../formInput";
import CancelModal from "../Modals/cancelModal";
import Select from "../../../../components/select";
import { ColorConsumer } from "../../../../context/colorContext";

//Icons
import ClientIcon from "@material-ui/icons/AccountBoxOutlined";

//Api

import PlansApi from "../../../../api/plans";
import ApiConfig from "../../../../api/auth";

//Routing
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import { log } from "util";

// Styles applied with classes
const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 1.5,
    textAlign: "center",
    backgroundColor: "white",
    color: theme.palette.text.secondary
  },
  formContainer: {
    display: "flex",
    flexWrap: "wrap",
    width: "100%"
  },
  card: {
    display: "flex"
  },
  details: {
    display: "flex",
    flexDirection: "column",
    height: 81
  },
  content: {
    flex: "1 0 auto"
  },
  cover: {
    width: 121
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: 5,
    paddingBottom: 5
  }
});

// Input validation types
const VALIDATION_TYPE = {
  alphanumeric: 1,
  text: 2,
  email: 3,
  numeric: 4,
  NIF: 5
};

class NewClient extends React.Component {
  state = {
    isViewModalOpen: false,
    isNotificationOpen: false,
    notificationText: "",
    notificationType: null,
    isInputValidated: [],
    createColor: "#9D9D9D",
    buttonBackground: "#E2E2E2",
    isButtonDisabled: true,
    clientToBeCreated: {},
    selectedPlan: null,
    planList: [],
    avatar:
      "data:image/png;base64,",
    color: null
  };

  componentDidMount = () => {
    this.getPlanList();
  };

  ///////////////////////////////////////////////////////////////
  // CLIENT
  ///////////////////////////////////////////////////////////////

  /**
   * Check if form fields are empty or with the wrong format, for validation
   * @param {string} name: Name of the form input
   * @param {boolean} isValidated: Is the input filled correctly?
   * @param {string} value: Value of the input
   */
  checkInputValidation = (name, isValidated, value) => {
    let inputObject = {
      name: name,
      isValidated: isValidated,
      value: value
    };

    let auxArray = [];

    if (inputObject.isValidated === null) {
      auxArray = this.state.isInputValidated;
      auxArray.push(inputObject);
    } else {
      auxArray = this.state.isInputValidated;
      auxArray.forEach(element => {
        if (element.name === name) {
          element.isValidated = isValidated;
          element.value = value;
        }
      });
    }

    this.setState({
      isInputValidated: auxArray
    });

    this.enableButton();
  };

  //If the form is filled correctly, enable "CREATE" button
  enableButton = async () => {
    let auxArray = [];

    const compañia = this.state.isInputValidated.filter(
      input => input.name == "compañia"
    );

    this.state.isInputValidated.forEach(element => {
      if (element.isValidated) {
        auxArray.push(element.name);
      }
    });

    if (compañia.value == "") {
      if (
        auxArray.length === this.state.isInputValidated.length - 1 &&
        this.state.selectedPlan !== null
      ) {
        this.setState({
          isButtonDisabled: false,
          createColor: "white",
          buttonBackground: "#6AD59F"
        });
      } else {
        this.setState({
          isButtonDisabled: true,
          createColor: "#9D9D9D",
          buttonBackground: "#E2E2E2"
        });
      }
    } else {
      if (
        auxArray.length === this.state.isInputValidated.length &&
        this.state.selectedPlan !== null
      ) {
        this.setState({
          isButtonDisabled: false,
          createColor: "white",
          buttonBackground: "#6AD59F"
        });
      } else {
        this.setState({
          isButtonDisabled: true,
          createColor: "#9D9D9D",
          buttonBackground: "#E2E2E2"
        });
      }
    }
  };

  ///////////////////////////////////////////////////////////////
  // PLANS
  ///////////////////////////////////////////////////////////////

  //Api call to get all plans
  getPlanList = () => {
    PlansApi.getAll()
      .then(response => {
        this.convertToSelectObject(response.plan);
      })
      .catch(e => {
        this.openNotification(e.toString(), "error");
      });
  };

  /**
   * Check if form fields are empty or with the wrong format, for validation
   * @param {array} planList: List of plans in api format
   */
  convertToSelectObject = planList => {
    let auxArray = [];
    planList.forEach(element => {
      let selectablePlan = {
        id: element.id,
        name: element.description + " / " + element.quote + "GB"
      };
      auxArray.push(selectablePlan);
    });

    this.setState({
      planList: auxArray
    });
  };

  /**
   * Select plan
   * @param {number} plan: Selected plan name
   */

  handlePlanSelect = async plan => {
    await this.setState({
      selectedPlan: plan
    });

    this.enableButton();
  };

  ///////////////////////////////////////////////////////////////
  // GENERAL FUNC.
  ///////////////////////////////////////////////////////////////

  closeModal = () => {
    this.setState({
      isViewModalOpen: false
    });
  };

  openModal = () => {
    this.setState({
      isViewModalOpen: true
    });
  };

  closeNotification = () => {
    this.setState({
      isNotificationOpen: false
    });
  };

  /**
   * Open snackbar notification
   * @param {string} text: Message showed in the snackbar
   * @param {string} type: Error, success, warning, etc-
   */
  openNotification = (text, type) => {
    this.setState({
      isNotificationOpen: true,
      notificationText: text,
      notificationType: type
    });
  };

  setColor = color => {
    this.setState({
      color: color.hex
    });
  };

  setAvatar = imageBase64 => {
    this.setState({
      avatar: imageBase64
    });
  };

  validateMail = () => {
    const email = this.state.isInputValidated.find(formInput => {
      if (formInput.name === "email") {
        return formInput;
      }
    }).value;

    ApiConfig.checkEmailRepetition({ email })
      .then(response => {
        if (response === true) {
          this.props.history.push("/newdepartments", {
            clientInformation: this.state.isInputValidated,
            plan: this.state.selectedPlan,
            color: this.state.color,
            avatar: this.state.avatar
          });
        } else {
          this.openNotification(
            "Este email ya está en uso. Debe elegir un email único",
            "warning"
          );
        }
      })
      .catch(e => {
        this.openNotification("Hubo un error validando el email", "error");
      });
  };
  render() {
    const { classes } = this.props;

    return (
      <div>
        <CancelModal
          handleModalOpen={this.state.isViewModalOpen}
          handleModalNo={this.closeModal}
        />

        <Notification
          message={this.state.notificationText}
          type={this.state.notificationType}
          handleNotificationOpen={this.state.isNotificationOpen}
          handleNotificationClose={this.closeNotification}
        />
        <Grid container spacing={24}>
          {/* Clients & Users */}
          <Grid item xs={12}>
            <Typography variant="h2"> Rellenar datos personales</Typography>
          </Grid>

          <Grid item xs={12}>
            <Paper className={classes.paper}>
              <Grid item className={classes.paper} xs={12}>
                <Typography
                  variant="caption"
                  gutterBottom
                  style={{
                    textAlign: "left",
                    paddingTop: 0,
                    color: "#A3A3A3"
                  }}
                >
                  <ClientIcon
                    style={{
                      paddingTop: 0,
                      verticalAlign: "sub"
                    }}
                  />
                  DATOS DEL CLIENTE
                </Typography>
                <form>
                  <Grid container>
                    <Grid
                      item
                      className={classes.paper}
                      xs={9}
                      style={{ backgroundColor: "#f6f6f6" }}
                    >
                      <Grid container>
                        <Grid
                          item
                          className={classes.paper}
                          xs={6}
                          style={{
                            backgroundColor: "#f6f6f6"
                          }}
                        >
                          <div
                            style={{
                              margin: "5px 5px 5px 30px"
                            }}
                          >
                            <div>
                              <Input
                                tabIndex="1"
                                checkInputValidation={this.checkInputValidation}
                                type={VALIDATION_TYPE.text}
                                name="nombre"
                                label="Nombre"
                                required={true}
                              />
                            </div>
                            <div>
                              <Input
                                tabIndex="3"
                                checkInputValidation={this.checkInputValidation}
                                type={VALIDATION_TYPE.text}
                                name="compañia"
                                label="Compañía"
                                required={false}
                              />
                            </div>
                            <div
                              style={{
                                width: "100%",
                                marginTop: "15px",
                                borderStyle: "solid",
                                borderColor:
                                  this.state.selectedPlan === null
                                    ? "#FF5A5F"
                                    : "white",
                                borderRadius: "7px",
                                borderWidth: 2
                              }}
                            >
                              <Select
                                tabIndex="5"
                                label="Plan *"
                                departmentList={this.state.planList}
                                selectedValue={this.handlePlanSelect}
                              />
                            </div>
                          </div>
                        </Grid>
                        <Grid
                          item
                          className={classes.paper}
                          xs={6}
                          style={{
                            backgroundColor: "#f6f6f6",
                            borderRight: "2px solid #e1e1e1"
                          }}
                        >
                          <div style={{ margin: "5px 30px 5px 5px" }}>
                            <div>
                              <Input
                                tabIndex="2"
                                checkInputValidation={this.checkInputValidation}
                                type={VALIDATION_TYPE.text}
                                name="apellidos"
                                label="Apellidos"
                                required={true}
                              />
                            </div>
                            <div>
                              <Input
                                tabIndex="4"
                                checkInputValidation={this.checkInputValidation}
                                type={VALIDATION_TYPE.email}
                                name="email"
                                label="Email"
                                required={true}
                              />
                            </div>
                            <div>
                              <Input
                                tabIndex="6"
                                checkInputValidation={this.checkInputValidation}
                                type={VALIDATION_TYPE.NIF}
                                name="cifnif"
                                label="CIFNIF"
                                required={true}
                              />
                            </div>
                          </div>
                        </Grid>
                      </Grid>
                    </Grid>

                    <Grid
                      item
                      className={classes.paper}
                      xs={3}
                      style={{
                        backgroundColor: "#f6f6f6",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center"
                      }}
                    >
                      <div>
                        <div>
                          <label for="avatar">
                            <div>
                              <Card className={classes.card}>
                                <CardActionArea
                                  onClick={event => {
                                    //Select the input element and click to open file seatch dialog.
                                    event.target.parentElement.parentElement.parentElement.parentElement.click();
                                  }}
                                >
                                  <div className={classes.details}>
                                    <CardContent className={classes.content}>
                                      <Typography>
                                        {" "}
                                        Seleccionar logotipo{" "}
                                      </Typography>
                                    </CardContent>
                                  </div>
                                </CardActionArea>
                                <CardMedia
                                  className={classes.cover}
                                  image={this.state.avatar}
                                  title="Avatar"
                                />
                              </Card>
                            </div>

                            <ColorConsumer>
                              {({ updateAvatar }) => (
                                <input
                                  style={{
                                    display: "none",
                                    color: "transparent"
                                  }}
                                  type="file"
                                  id="avatar"
                                  name="avatar"
                                  accept="image/png, image/jpeg"
                                  onChange={event => {
                                    let getImage64 = new Promise(
                                      async (resolve, reject) => {
                                        const file = event.target.files[0];

                                        //resizer
                                        const options = {
                                          maxWidth: 100,
                                          quality: 0.6
                                        };

                                        let compressed_file = await resizer(
                                          file,
                                          options
                                        ).then(reducedImage => {
                                          return new Blob([reducedImage]);
                                        });

                                        var reader = new FileReader();
                                        
                                     
                                        reader.readAsDataURL(
                                          await compressed_file
                                        );

                                        reader.onloadend = function() {
                                          resolve(reader.result);
                                        };

                                        reader.onerror = function(error) {
                                          throw new Error(
                                            "Error cargando la imagen"
                                          );
                                        };
                                      }
                                    );

                                    getImage64.then(image64Base => {
                                      updateAvatar(image64Base);
                                      this.setAvatar(image64Base);
                                    });
                                  }}
                                />
                              )}
                            </ColorConsumer>
                          </label>
                        </div>
                        <br />
                        <ColorConsumer>
                          {({ updateColor }) => (
                            <TwitterPicker
                              colors={[
                                "#7ed3b2",
                                "#60a9a6", //VERDE
                                "#74b49b",
                                "#6e7da2", //AZUL
                                "#6c5ce7", //MORADO
                                "#ca3e47", //ROJO
                                "#f38181", //MARRON
                                "#d698b9", //ROSADO

                                "#f06f32", //NARANJA
                                "#f79f24" //AMARILLO
                              ]}
                              triangle="hide"
                              onChange={color => {
                                updateColor(color.hex);
                                this.setColor(color);
                              }}
                            />
                          )}
                        </ColorConsumer>
                      </div>
                    </Grid>
                  </Grid>
                </form>
              </Grid>
              <Button
                tabIndex="7"
                variant="outlined"
                onClick={() => {
                  this.validateMail();
                }}
                style={{
                  margin: 10,
                  color: this.state.createColor,
                  backgroundColor: this.state.buttonBackground
                }}
                disabled={this.state.isButtonDisabled}
              >
                {" "}
                GUARDAR{" "}
              </Button>

              <Button
                tabIndex="8"
                variant="outlined"
                style={{
                  margin: 10,
                  backgroundColor: "#FF5A5F",
                  color: "white"
                }}
                onClick={this.openModal}
              >
                {" "}
                Cancelar{" "}
              </Button>
            </Paper>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default withRouter(withStyles(styles)(NewClient));
