import "date-fns";
import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import CancelModal from "../Modals/cancelModal";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import DepartmentArray from "../departmentChipArray";

import { ColorConsumer } from "../../../../context/colorContext";
import { FillSpinner } from "react-spinners-kit";

import Notification from "../../../../components/notification";

import PlansApi from "../../../../api/plans";
import ClientsApi from "../../../../api/clients";
import ApiConfig from "../../../../api/config";

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit * 1.5,
    marginTop: 24,
    textAlign: "center",
    color: theme.palette.text.secondary,
    backgroundColor: "#f6f6f6"
  },
  list: {
    padding: theme.spacing.unit * 1.5,
    margin: 0,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  cardBase: {
    margin: 40
  },
  presentation: {
    borderRight: "2px dashed #D8D8D8"
  },
  avatar: {
    width: 100,
    height: 100,
    marginTop: 15,
    marginBottom: 10,
    borderStyle: "solid",
    borderWidth: 6,
    borderRadius: "50%"
  },
  company: {
    marginBottom: 15
  },
  information: {
    display: "grid",
    gridRowGap: 5,
    alignItems: "center",
    marginTop: 15,
    marginBottom: 15
  }
});

class Departments extends React.Component {
  state = {
    client: { departments: [] },
    planName: "",
    loading: false
  };
  static contextType = ColorConsumer;
  importClientInformation = () => {
    const [
      nombre,
      compañia,
      apellidos,
      email,
      cifnif
    ] = this.props.location.state.clientInformation;

    this.setState({
      client: {
        name: nombre.value,
        last_names: apellidos.value,
        cifnif: cifnif.value,
        email: email.value,
        company: compañia.value ? compañia.value : " ",
        planId: parseInt(this.props.location.state.plan),
        color: this.props.location.state.color || "#f6f6f6",
        avatar: this.props.location.state.avatar,
        departments: this.props.location.state.departments
      }
    });
  };
  componentDidMount = () => {
    this.importClientInformation();
    this.getPlansName();
    this.stringifyFields();
  };

  stringifyFields = () => {
    let auxArray = [...this.props.location.state.departments];

    auxArray.forEach(department => {
      department.fields.forEach(field => {
        field.is_visible = field.is_visible.toString();
        field.is_required = field.is_required.toString();
      });
    });

    this.setState({
      departments: auxArray
    });
  };

  getPlansName = () => {
    PlansApi.getById(this.props.location.state.plan).then(response => {
      this.setState({
        planName: response.plan.description
      });
    });
  };

  //Sleep in miliseconds
  sleep = ms => {
    return new Promise(resolve => setTimeout(resolve, ms));
  };

  createClient = () => {
    this.setState({
      loading: true
    });
    ClientsApi.create(this.state.client)
      .then(response => {
        this.openNotification(
          "El cliente ha sido creado con exito.",
          "success"
        );
        this.sleep(1500).then(() => {
          this.changeColor();

          this.props.history.push("/usermng");
        });
      })
      .catch(e => {
        this.openNotification(e.toString(), "error");
      });
  };
  closeModal = () => {
    this.setState({
      isViewModalOpen: false
    });
  };

  openModal = () => {
    this.setState({
      isViewModalOpen: true
    });
  };

  closeNotification = () => {
    this.setState({
      isNotificationOpen: false
    });
  };

  /**
   * Open snackbar notification
   * @param {string} text: Message showed in the snackbar
   * @param {string} type: Error, success, warning, etc-
   */
  openNotification = (text, type) => {
    this.setState({
      isNotificationOpen: true,
      notificationText: text,
      notificationType: type
    });
  };

  changeColor = async () => {
    if ((await ApiConfig.getUser()) == "admin") {
      this.context.updateColor("#74b49b");
      this.context.updateAvatar(
        "data:image/png;base64,"
      );
    }
  };
  render() {
    const { classes } = this.props;

    return (
      <div>
        <CancelModal
          handleModalOpen={this.state.isViewModalOpen}
          handleModalNo={this.closeModal}
        />

        <Notification
          message={this.state.notificationText}
          type={this.state.notificationType}
          handleNotificationOpen={this.state.isNotificationOpen}
          handleNotificationClose={this.closeNotification}
        />
        <Grid item xs={12}>
          <Typography variant="h2">Crear cliente</Typography>
        </Grid>

        <Paper className={classes.paper}>
          {this.state.loading && (
            <ColorConsumer>
              {({ color }) => (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    padding: 30
                  }}
                >
                  {" "}
                  <FillSpinner size={30} color={color} loading={true} />
                </div>
              )}
            </ColorConsumer>
          )}
          {!this.state.loading && (
            <Grid container>
              <Grid item className={classes.list} xs={2} />
              <Grid item className={classes.list} xs={4}>
                <div>
                  <Paper className={classes.cardBase}>
                    <Grid container>
                      <Grid item xs={6} className={classes.presentation}>
                        <img
                          style={{
                            borderColor: this.props.location.state.color
                          }}
                          className={classes.avatar}
                          src={this.state.client.avatar}
                        />
                        <Typography variant="h5">
                          {this.state.client.company}
                        </Typography>
                        {/* <Typography
                        style={{ color: this.props.location.state.color }}
                        variant="overline"
                        className={classes.company}
                      >
                        {this.state.client.company}
                      </Typography> */}
                      </Grid>
                      <Grid item xs={6} className={classes.information}>
                        <Typography
                          variant="subtitle1"
                          style={{ fontWeight: 700 }}
                        >
                          {" "}
                          Email:{" "}
                        </Typography>
                        <Typography variant="subtitle2">
                          {" "}
                          {this.state.client.email}{" "}
                        </Typography>
                        <Typography
                          variant="subtitle1"
                          style={{ fontWeight: 700 }}
                        >
                          {" "}
                          CIFNIF:{" "}
                        </Typography>
                        <Typography variant="subtitle2">
                          {" "}
                          {this.state.client.cifnif}{" "}
                        </Typography>
                        <Typography
                          variant="subtitle1"
                          style={{ fontWeight: 700 }}
                        >
                          {" "}
                          Plan:{" "}
                        </Typography>
                        <Typography variant="subtitle2">
                          {" "}
                          {this.state.planName}{" "}
                        </Typography>
                      </Grid>
                    </Grid>
                  </Paper>
                </div>
              </Grid>
              <Grid item className={classes.list} xs={4}>
                <Paper
                  className={classes.cardBase}
                  style={{
                    maxHeight: 600,
                    overflowY: "auto",
                    backgroundColor: "#D8D8D8"
                  }}
                >
                  {this.state.client.departments.map(department => {
                    return (
                      <ExpansionPanel>
                        <ExpansionPanelSummary
                          expandIcon={<ExpandMoreIcon />}
                          aria-controls="panel1a-content"
                          id="panel1a-header"
                        >
                          <Typography className={classes.heading}>
                            {department.name}
                          </Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                          <DepartmentArray departments={department.fields} />
                        </ExpansionPanelDetails>
                      </ExpansionPanel>
                    );
                  })}
                </Paper>
              </Grid>
              <Grid item className={classes.list} xs={2} />
            </Grid>
          )}
          <Button
            disabled={this.state.loading}
            variant="outlined"
            style={{
              margin: 10,
              color: this.state.loading ? "#9D9D9D" : "white",
              backgroundColor: this.state.loading ? "#E2E2E2" : "#6AD59F"
            }}
            onClick={() => {
              this.createClient();
            }}
          >
            {" "}
            CREAR{" "}
          </Button>
          <Button
            disabled={this.state.loading}
            variant="outlined"
            style={{
              margin: 10,
              backgroundColor: this.state.loading ? "#E2E2E2" : "#FF5A5F",
              color: this.state.loading ? "#9D9D9D" : "white"
            }}
            onClick={this.openModal}
          >
            {" "}
            Cancelar{" "}
          </Button>
        </Paper>
      </div>
    );
  }
}

Departments.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Departments);
